.nav-button{
  background-color: #6d04aa; /* Green */
  border: none;
  color: white;
  padding: 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 10%;
  width:30%;
}
.paragraph{
  color:rgb(253, 253, 253);
  font-family:Arial, Helvetica, sans-serif;
  font-weight: bold;
  right:50%;
  background-image: url(cryptoscape.jpg);
  border-radius: 5%;
}
.App-link {
  color: #61dafb;
}
